body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-7xs: 6px;
  --font-size-6xs: 7px;
  --font-size-9xs: 4px;
  --font-size-8xs: 5px;
  --font-size-4xs: 9px;
  --font-size-5xs: 8px;
  --font-size-3xs-5: 9.5px;
  --font-size-4xs-5: 8.5px;

  /* Colors */
  --color-gray-100: #fbfdfb;
  --color-gray-200: rgba(0, 0, 0, 0);
  --color-dimgray-100: #565656;
  --color-silver-100: #bdbdbd;
  --color-gainsboro: #e8e8e8;
  --color-whitesmoke: rgba(247, 248, 247, 1);

  /* Gaps */
  --gap-xl: 20px;
  --gap-smi: 13px;
  --gap-10xs: 3px;
  --gap-7xs: 6px;
  --gap-11xs: 2px;
  --gap-12xs: 1px;
  --gap-9xs: 4px;
  --gap-xs: 12px;
  --gap-3xl: 22px;
  --gap-3xs: 10px;
  --gap-8xs: 5px;

  /* Paddings */
  --padding-smi: 13px;
  --padding-10xs: 3px;
  --padding-11xs: 2px;
  --padding-3xs: 10px;
  --padding-7xs: 6px;
  --padding-12xs: 1px;
  --padding-9xs: 4px;
  --padding-8xs: 5px;
  --padding-mini: 15px;
  --padding-xs: 12px;
  --padding-2xs: 11px;
  --padding-6xs: 7px;
  --padding-4xs: 9px;
  --padding-5xs: 8px;
  --padding-8xl: 27px;
  --padding-61xl: 80px;
  --padding-xl: 20px;
  --padding-5xl: 24px;

  /* Border radiuses */
  --br-10xl-3: 29.3px;
  --br-7xs-5: 5.5px;
  --br-4xs-3: 8.3px;
  --br-12xs: 1px;
  --br-7xs-8: 5.8px;
  --br-8xs-8: 4.8px;
  --br-10xs-5: 2.5px;
  --br-9xs-8: 3.8px;
  --br-10xs: 3px;
  --br-10xs-3: 2.3px;
  --br-8xs-5: 4.5px;
  --br-9xs-3: 3.3px;
}
